}
.company-logo:hover img {
  opacity: 1;
}
.company-logo img {
  opacity: 0.6;
  transition: all 0.3s ease;
}
label.ant-checkbox-group-item {
  margin-bottom: 10px;
}
label.ant-radio-wrapper {
  line-height: 2;
}
.ant-dropdown .ant-dropdown-menu .ant-dropdown-menu-item {
  padding: 10px 20px;
}
.floating-container {
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
  left: 0;
}
#outline {
  -webkit-animation: 0.38s ease-in outline;
          animation: 0.38s ease-in outline;
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transform-origin: center;
          transform-origin: center;
}
#white-circle {
  -webkit-animation: 0.35s ease-in 0.35s forwards circle;
          animation: 0.35s ease-in 0.35s forwards circle;
  -webkit-transform: none;
          transform: none;
  -webkit-transform-origin: center;
          transform-origin: center;
}
#check {
  -webkit-animation: 0.34s cubic-bezier(0.65, 0, 1, 1) 0.8s forwards check;
          animation: 0.34s cubic-bezier(0.65, 0, 1, 1) 0.8s forwards check;
  stroke-dasharray: 0, 75px;
}
#check-group {
  -webkit-animation: 0.32s ease-in-out 1.03s check-group;
          animation: 0.32s ease-in-out 1.03s check-group;
  -webkit-transform-origin: center;
          transform-origin: center;
}
@media screen and (max-width: 414px) {
  .px0-mobile {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }
  .p0-mobile {
    padding: 0 !important;
  }
  .mb0-mobile {
    margin-bottom: 0 !important;
  }
  .mb2em-mobile {
    margin-bottom: 2em !important;
  }
}
@-webkit-keyframes outline {
  from {
    stroke-dasharray: 0, 345.576px;
  }
  to {
    stroke-dasharray: 345.576px, 345.576px;
  }
}
@keyframes outline {
  from {
    stroke-dasharray: 0, 345.576px;
  }
  to {
    stroke-dasharray: 345.576px, 345.576px;
  }
}
@-webkit-keyframes check-group {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes check-group {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(1.09);
            transform: scale(1.09);
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes check {
  from {
    stroke-dasharray: 0, 75px;
  }
  to {
    stroke-dasharray: 75px, 75px;
  }
}
@keyframes check {
  from {
    stroke-dasharray: 0, 75px;
  }
  to {
    stroke-dasharray: 75px, 75px;
  }
}
@-webkit-keyframes circle {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@keyframes circle {
  from {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  to {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
